.about {
  padding: 1rem;
  @media (min-width: 600px) {
    padding: 2rem; }
  @media (min-width: 900px) {
    padding: 3rem 2rem; }
  @media (min-width: 1200px) {
    padding: 4rem 2rem; }
  &__section {
    padding-top: calc(var(--nav-height) + var(--space-lg)); }
  &__title {
    color: var(--title-color); }
  &__info {
    line-height: 1.3;
    margin-bottom: 25px;
    max-width: 786px;
    &:last-of-type {
      margin-bottom: 40px; }
    @media (min-width: 900px) {
      font-size: 18px; } } }
