.project {
  &__img {
    width: 100%;
    height: 100%; }
  &__card {
    border-radius: var(--border-radius-lg);
    background: var(--bg-alt);
    box-shadow: var(--shadow-md);
    margin: 0 auto;
    &-name {
      color: var(--title-color);
      padding-top: var(--space-md); }
    &-name, &-description {
      text-align: center;
      margin: 0 0 var(--space-md) 0; }
    &-footer {
      padding: 1rem;
      display: flex;
      align-items: center;
      justify-content: center;
      .fill {
        flex: 1; } }
    &-link {
      text-align: center;
      font-weight: 600;
      flex: 4;
      height: 50px;
      line-height: 47px;
      border-radius: 4px;
      border: 1px solid var(--title-color);
      text-decoration: none;
      color: var(--text-color-alt);
      transition: 150ms color, 150ms background;
      &:hover {
        background: #1a7b90;
        color: #FFF; }
      &:active {
        background-color: #155f6f; } }
    &-github-link {
      flex: 1;
      display: inline-block;
      margin-left: 1rem;
      .github-logo {
        width: 50px; } } } }

.projects {
  &__list {
    display: grid;
    width: 100%;
    grid-gap: var(--space-lg);
    @media (min-width: 786px) {
      grid-template-columns: repeat(2, 1fr); }
    &-item {
      width: 100%; } } }

.tech-stack {
  padding: 1rem;
  &__header {
    color: var(--title-color);
    text-align: center; }
  &__title {
    margin-bottom: 0.5rem;
    color: var(--title-color); }
  &__category {
    margin-bottom: 1rem; }
  &__category-container {
    text-align: center;
    justify-content: space-evenly; }
  &__list {
    &-item {
      padding: 0.5rem 1.5rem; } } }

@media (min-width: 600px) and (max-width: 785px) {
  .tech-stack {
    &__category {
      flex: 1;
      padding: 1rem;
      margin-bottom: 0; }
    &__category-container {
      display: flex;
      text-align: left;
      justify-content: space-evenly; } } }

@media (min-width: 1250px) {
  .tech-stack {
    &__category {
      flex: 1;
      padding: 1rem;
      margin-bottom: 0; }
    &__category-container {
      display: flex;
      text-align: left;
      justify-content: space-evenly; } } }
